<template lang="jade">
.content
  el-header
    el-row(type="flex", justify="space-between")
      el-col(:span="6" style='width:auto;')
        span.logo()
          .title 征信中心
          i(class="fa fa-bars",@click="toggleSideBar",:class="{'menu-opend':sidebar.opened,'menu-folded':!sidebar.opened}")
      el-col.t-right(:span="12")    
        //- el-button(type="text",@click="toogleScreen")    
          i(class="fa menu-btn",:class="{'fa-expand':!isFullScreen,'fa-compress':isFullScreen}")
        el-dropdown.navbar-dropdown(:hide-on-click="false")
          .el-dropdown-link
            //- img.heading-avatar(src="~@/assets/account.gif") 
            span {{ name || '未知' }}
            i.el-icon-caret-bottom
          el-dropdown-menu(slot="dropdown")
            el-dropdown-item(@click.native="changePwd")
              i.fa.fa-unlock
              span 修改密码
            el-dropdown-item(@click.native="logout")
              i.fa.fa-sign-out
              span 退出
  el-dialog(title="修改密码" ,:visible.sync="isShowPw", :close-on-click-modal="false", width='390px')
    el-form(label-width="120px",label-position="left", style="margin-top:30px;")
      el-form-item(label="输入旧密码:")
        el-input(v-model="password.oldPwd", type="password", placeholder="请输入旧密码")
      el-form-item(label="输入新密码:")
        el-input(v-model="password.newPwd", type="password", placeholder="不少于6位字符，请勿与旧密码相同",  @keyup.native="checkIntensity")
      el-form-item(label="重复新密码:")
        el-input(v-model="password.newPwdRe", type="password", placeholder="不少于6位字符，请勿与旧密码相同")
      el-form-item(label="密码强度:")
        .detectionBtn
          span(:class="{weak: isShoWeak}") 弱
          span(:class="{middle: isShowMiddle}") 中
          span(:class="{better: isShowBetter}") 强
    div(slot="footer" ,class="dialog-footer")
      el-button(type="default", @click="isShowPw = false", style="margin-right: 10px;") 取消
      el-button(type="primary", @click="submit") 确定
</template>

<script>
const checkStrong = (val) => {
  var modes = 0
  if (val.length < 6) return 0
  if (/\d/.test(val)) modes++ // 数字
  if (/[a-z]/.test(val)) modes++ // 小写
  if (/[A-Z]/.test(val)) modes++ // 大写
  if (/\W/.test(val)) modes++ // 特殊字符
  if (val.length > 26) return 4
  return modes
}
import { mapGetters } from 'vuex'

export default {
  data() {
    return {
      isFullScreen: false,
      name: localStorage.getItem('u') && JSON.parse(localStorage.getItem('u')).username,
      password: {
        oldPwd: '',
        newPwd: '',
        newPwdRe: ''
      },
      isShowPw: false,
      isShoWeak: false,
      isShowMiddle: false,
      isShowBetter: false
    }
  },
  computed: {
    ...mapGetters([
      'sidebar'
    ])
  },
  methods: {
    toggleSideBar() {
      this.$store.dispatch('toggleSideBar')
    },
    logout() {
      var params = {
        token: JSON.parse(localStorage.getItem('u')).token,
        appCode: this.$store.state.app.appCode
      }
      this.$store.dispatch('LogOut', params).then((res) => {
        location.reload()
      }).catch(() => {
        this.$store.dispatch('FedLogOut').then(() => {
          location.reload()
        })
      })
    },
    checkIntensity() {
      var num = checkStrong(this.password.newPwd)
      switch (num) {
        case 1:
          this.isShoWeak = true
          this.isShowMiddle = false
          this.isShowBetter = false
          break
        case 2:
          this.isShoWeak = false
          this.isShowMiddle = true
          this.isShowBetter = false
          break
        case 3:
        case 4:
          this.isShoWeak = false
          this.isShowMiddle = false
          this.isShowBetter = true
          break
        case 0:
          this.isShoWeak = false
          this.isShowMiddle = false
          this.isShowBetter = false
          break
        default:
          this.isShoWeak = true
          this.isShowMiddle = false
          this.isShowBetter = false
          break
      }
    },
    changePwd() {
      this.isShowPw = true
      this.password.oldPwd = ''
      this.password.newPwd = ''
      this.password.newPwdRe = ''
    },
    submit() {
      if (
        !this.password.newPwd ||
        !this.password.oldPwd ||
        !this.password.newPwdRe
      ) {
        this.$notify({
          title: '警告',
          message: '修改失败, 新密码与旧密码必需输入',
          type: 'warning'
        })
        this.password.newPwd = ''
        this.password.newPwdRe = ''
        return
      } else if (this.password.newPwd !== this.password.newPwdRe) {
        this.$notify({
          title: '警告',
          message: '修改失败，两次密码不一致',
          type: 'warning'
        })
        this.password.newPwd = ''
        this.password.newPwdRe = ''
        return
      } else if (this.password.newPwd === this.password.oldPwd) {
        this.$notify({
          title: '警告',
          message: '修改失败, 新密码不能与旧密码相同',
          type: 'warning'
        })
        this.password.newPwd = ''
        this.password.newPwdRe = ''
        return
      } else if (this.password.newPwd.length < 6) {
        this.$notify({
          title: '警告',
          message: '密码不能少于6位字符',
          type: 'warning'
        })
        this.password.newPwd = ''
        this.password.newPwdRe = ''
        return
      }
      var params = {
        appCode: this.$store.state.app.appCode,
        userPwd: this.$md5(this.password.oldPwd),
        changePwd: this.$md5(this.password.newPwd)
      }
      this.$store.dispatch('ChangePwd', params).then((res) => {
        location.reload()
      }).catch(() => {
        this.$store.dispatch('FedLogOut').then(() => {
          location.reload()
        })
      })
    },
    toogleScreen() {
      this.isFullScreen = !this.isFullScreen
      if (this.isFullScreen) {
        this.requestFullScreen()
      } else {
        this.exitFullscreen()
      }
    },
    requestFullScreen() {
      var de = document.documentElement
      if (de.requestFullscreen) {
        de.requestFullscreen()
      } else if (de.mozRequestFullScreen) {
        de.mozRequestFullScreen()
      } else if (de.webkitRequestFullScreen) {
        de.webkitRequestFullScreen()
      }
    },
    exitFullscreen() {
      var de = document
      if (de.exitFullscreen) {
        de.exitFullscreen()
      } else if (de.mozCancelFullScreen) {
        de.mozCancelFullScreen()
      } else if (de.webkitCancelFullScreen) {
        de.webkitCancelFullScreen()
      }
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.el-header {
    height: 49px !important;
    line-height: 50px !important;
    border-bottom: 1px solid rgba(0, 0, 0, 0.8);
    background-color: #111;
    position: fixed !important;
    width: 100% !important;
    .el-dropdown-link:hover {
      height: 48px !important;
    }
    .menu-btn {
      color: #ccc;
    }
  }

  .logo {
    margin-right: 20px;
    display: inline-block;
    text-align: left;
    font-size: 20px;
    color: rgb(34, 34, 34);
    img {
        margin-top: 20px;
        width: 150px;
        // transform: scale(.8);
    }
    .title {
      display: inline-block;
      font-size: 16px;
      font-weight: 500;
      width: 135px;
      text-align: left;
      color: #fff;
    }
    .fa-bars {
        margin-left: 25px;
        cursor: pointer;
        color: #ccc;
    }
    .menu-opend {
        transform: rotate(0deg);
        transition: transform 0.5s;
    }
    .menu-folded {
        transform: rotate(90deg);
        transition: transform 0.5s;
    }
  }
  .t-right {
    text-align: right;
  }
  .heading-avatar {
    width: 30px;
    height: 30px;
    border-radius: 30px;
    margin-right: 10px;
    border: 1px solid #ddd;
    overflow: hidden;
  }
  .el-dropdown {
    color: #ccc;
  }
  .el-dropdown-link {
    cursor: pointer;
    height: 50px;
    line-height: 50px;
    padding: 0 15px;
    min-width: 50px;
    text-align: center;
    margin-left: 20px;
    span {
      margin-right: 5px;
      font-weight: 500;
    }
    i {
      font-size: 18px;
    }
    img {
      vertical-align: middle;
    }
    &:hover {
      color: #fff;
    }
  }
  .detectionBtn{
    span{
        display: inline-block;
        width: 60px;
        height: 20px;
        line-height: 20px;
        border: 1px solid #ccc;
        border-radius: 4px;
        background: #fff;
        margin-right: 5px;
        text-align: center;
    }
    .weak{
        background: red;
        color: #fff
    }
    .middle{
        background: yellow;
        color: #fff
    }
    .better{
        background: green;
        color: #fff
    }
  }
</style>




