import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

import Layout from '../views/layout/Layout'

export const constantRouterMap = [
  { path: '/login', component: () => import('@/views/login/index'), hidden: true },
  {
    path: '/404',
    component: () =>
        import('@/views/404'),
    hidden: true
  },
  {
    path: '/',
    component: Layout,
    redirect: '/home',
    hidden: true,
    children: [
      {
        path: 'default',
        name: 'Default',
        component: () =>
          import('@/views/default/index')
      },
      {
        path: 'home',
        name: 'Home',
        component: () =>
            import('@/views/creditProduction/list')
      }
    ]
  },
  {
    path: '/dataSearch',
    component: Layout,
    redirect: '/list',
    hidden: true,
    children: [{
      path: 'list',
      name: 'dataSearchList',
      component: () =>
        import('@/views/dataSearch/list')
    }]
  }
]
export const asyncRouterMap = [
  // {
  //   path: '/carMonitor',
  //   component: Layout,
  //   redirect: '/car',
  //   hidden: true,
  //   children: [{
  //     path: 'car',
  //     name: 'Car',
  //     component: () =>
  //       import('@/views/carMonitor/car')
  //   }]
  // },
  {
    path: '/dataSearch',
    component: Layout,
    redirect: '/list',
    hidden: true,
    children: [{
      path: 'list',
      name: 'dataSearchList',
      component: () =>
        import('@/views/dataSearch/list')
    }]
  },
  {
    path: '/menuManage',
    component: Layout,
    redirect: '/menuManage/list',
    name: 'MenuManage',
    meta: {
      title: 'MenuManage',
      icon: '',
      roles: ['admin']
    },
    children: [
      {
        path: 'list',
        name: 'List',
        component: () =>
            import('@/views/menuManage/list'),
        meta: {
          title: '菜单列表',
          icon: '',
          roles: ['admin']
        }
      }
    ]
  },
  {
    path: '*',
    redirect: '/404',
    hidden: true
  }
]

export default new Router({
  // mode: 'history', //后端支持可开
  scrollBehavior: () => ({
    y: 0
  }),
  routes: constantRouterMap
})



